@keyframes slideDown {
    0%{
        transform: translateY(-100%);
    }
    100%{
        transform: translateY(0);
    }
}

@keyframes faded {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

.slideDown {
    animation: slideDown 2000ms;
}

.faded {
    animation-delay: 1500ms;
    animation: faded 3000ms;
}